/**
* Created by vouill on 11/13/17.
*/

<template>
  <!-- <div>
    <form class="login" @submit.prevent="login">
      <h1>Sign in</h1>
      <label>User name</label>
      <input required v-model="email" type="text" placeholder="Snoopy"/>
      <label>Password</label>
      <input required v-model="password" type="password" placeholder="Password"/>
      <hr/>
      <button type="submit">Login</button>
    </form>
  </div> -->





  <div class="text-center vertical-center">
      <form @submit.prevent="register" class="form-signin">
        <h1 class="h3 mb-3 font-weight-normal">Altkeep</h1>

        <label for="inputName" class="sr-only">Name</label>
        <input v-model="name" type="name" id="inputName" class="form-control" placeholder="Name" required autofocus>

        <label for="inputEmail" class="sr-only">Email address</label>
        <input v-model="email" type="email" id="inputEmail" class="form-control" placeholder="Email address" required>

        <label for="inputPassword" class="sr-only">Password</label>
        <input v-model="password" type="password" id="inputPassword" class="form-control" placeholder="Password" required>

        <div class="checkbox mb-3">

        </div>
        <button class="btn btn-lg btn-primary btn-block" type="submit">Signup</button>
    
        <br>
        Already have an account ? <a @click="gotologin" href="#">Login</a>
     
        <p class="mt-5 mb-3 text-muted">&copy; 2018-2019</p>
      </form>
   </div>






</template>



<script>
  import {AUTH_REGISTER} from 'actions/auth'

  export default {
    name: 'register',
    data () {
      return {
        name: '',
        email: '',
        password: '',
      }
    },
    methods: {
      register: function () {
        const { name, email, password } = this
        this.$store.dispatch(AUTH_REGISTER, { name, email, password }).then(() => {
          this.$router.push('/')
        })
      },
      gotologin(){
         this.$root.$emit('backtologin');

      }
    
    },
  }
</script>

<style scoped>

.form-signin {
height: 100%;
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}
.form-signin input[type="email"] {
  margin-bottom: -1px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
}
.form-signin input[type="password"] {
  margin-bottom: 10px;
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}
.vertical-center {
  min-height: 100%;  /* Fallback for browsers do NOT support vh unit */
  min-height: 100vh; /* These two lines are counted as one :-)       */

  display: flex;
  align-items: center;
}
   

  
</style>



